.ad-label {
display: flex;
padding-left: 45px;
padding-right: 45px;
margin-bottom: 56px;
justify-content: space-between;
height: 100px;
background-color: #232222;
}

.ad-label__wrapper {
display: flex;
justify-content: space-between;
}

.ad-label__image{
width: 152px;
}

.ad-label__slogan{
margin-top: 18px;
font-size: 24px;
line-height: 28px;   
font-weight: 700;
color: #fff;
}

.ad-label__logo-image{
width: 134px;
}

.ad-label__logo-image__item{
width: 100%;
height: 100%;
}
@media (width < 730px) {
 .ad-label{
    padding-left: 10px;
    padding-right: 10px;
    margin-bottom: 28px;
 }
 .ad-label__slogan{
    margin-top: 18px;
    font-size: 18px;
    line-height: 24px;   
    font-weight: 700;
    color: #fff;
    }
}
@media (width < 450px) {
.ad-label {
    flex-direction: column;
    height: auto;
   align-items:center;
   padding-top: 10px;
  
}
.ad-label__wrapper {
    flex-direction: column;
    align-items:center;
}
.ad-label__logo-image{
    width: 94px;
    }
.ad-label__image{
    width: 104px;
}

}