.character-info{
display: flex;
gap: 56px;
}

.character-info__character-image{
flex-basis: 294px;
flex-shrink: 0;
height: 294px;
}

 .character-info__character-image__item{
width: 100%;
height: 100%; 
} 

.character-info__wrapper {
    flex-basis: 200px;
    flex-grow: 1;
    flex-shrink: 0;
    max-width: 550px;
}
.character-info__name{
font-size: 22px;
line-height: 26px;
font-weight: 700;
margin-bottom: 28px;
}

.character-info__description{
font-size: 18px;
line-height: 21px;
}
@media (width < 626px){
    .character-info{
    display: flex;
    flex-direction: column;
    gap: 10px;
    }
    .character-info__description{
    font-size: 16px;
    line-height: 18px;
    }
    .character-info__name{
        font-size: 18px;
        line-height: 24px;
        font-weight: 700;
    }

}